.equips-concret-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 32px;
}

.team-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  margin-bottom: 50px;
}

.team-logo-large {
  width: 100px;
  height: auto;
}

.team-name-title {
  font-size: 2rem;
  font-weight: bold;
  color: white;
  margin: 0;
}

.tables-wrapper {
  display: flex;
  flex-direction: column;
  gap: 40px;
  align-items: center;
  width: 100%;
}

.table-row {
  display: flex;
  justify-content: center;
  gap: 32px;
  flex-wrap: wrap;
  align-items: flex-start;
}

.table {
  background-color: #5b3f76;
  padding: 20px;
  border-radius: 16px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.2);
  min-width: 300px;
  max-width: 500px;
}

.table h2 {
  color: white;
  margin-bottom: 12px;
  text-align: center;
}

.table table {
  width: 100%;
  border-collapse: collapse;
  color: white;
}

.table th,
.table td {
  padding: 8px;
  text-align: center;
}

.table th {
  background-color: #7a579b;
  color: white;
  font-weight: bold;
}

.table tr:nth-child(even) {
  background-color: rgba(255, 255, 255, 0.05);
}
